<template>
	<div >
		<div class="tradingRecord qrcode">
			<div style="height:1px"></div>
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a ></a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p></p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardNo" width="50%">
				</div>
				<p></p>
			</div>
		</div>

	</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	urlCardNo:''
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				console.log(window.location);
				let url=window.location.origin;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				_this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.wxshareCard_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('wxshareCard',data)
	                	if(data.code==0){
	                		wx.config({
								debug: true, // ,apialertpclogpc
							    appId: data.data.appid, // 
							    timestamp: data.data.timestamp, // 
							    nonceStr: data.data.nonce_str, // 
							    signature: data.data.signature,// 1
							    jsApiList: ["onMenuShareAppMessage"] // JSJS2
							});
							wx.ready(function(){
								//
								 wx.onMenuShareAppMessage({
								    title: "!", // 
								    link: url+'/#/shareQRCode/'+_this.busId, // 
								    imgUrl: _this.urlCardNo, // 
								    success: function () { 
								        // 
								    },
								    cancel: function () { 
								        // 
								    }
								}); 
							});
	                	}
	                }
				})
				console.log('urlCardNo',_this.urlCardNo)
			}
		},
		created:function(){
	    	var _this=this;
	    	_this.loadData();
	        
	    }
	}
</script>
<style scope>

</style>


